import { Component, OnInit } from '@angular/core'
import { DirektoriPelamarService } from 'src/app/sdk/hr/services'
import { DirektoriPermintaanPegawaiListService } from 'src/app/sdk/hr/services/direktori-permintaan-pegawai-list.service'
import { NgxSpinnerService } from 'ngx-spinner'
import { NzNotificationService } from 'ng-zorro-antd'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Router, ActivatedRoute } from '@angular/router'
import { Pelamar } from 'src/app/sdk/hr/models'
import { Permintaan_pegawai_list } from 'src/app/sdk/hr/models/permintaan-_pegawai-_list'
import { FormControl, Validators } from '@angular/forms'

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss'],
})
export class EditComponent implements OnInit {
    constructor(
        private direktoriPelamarService: DirektoriPelamarService,
        private direktoriPermintaanPegawaiListService: DirektoriPermintaanPegawaiListService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }

    dataPelamar: Pelamar = {}
    listPermintaan: Permintaan_pegawai_list[] = []

    id_pelamar: string = ''

    ngOnInit() {
        this.id_pelamar = this.activatedRoute.snapshot.paramMap.get('id_pelamar')
        this.getPreeData()
        this.getData()
        this.form()
    }

    getPreeData() {
        this.direktoriPermintaanPegawaiListService
            .getDirektoriPermintaanPegawaiLists()
            .subscribe(data => {
                this.listPermintaan = data.data
            })
    }

    getData() {
        this.spinner.show()
        this.direktoriPelamarService.getDirektoriPelamarsId(this.id_pelamar).subscribe(data => {
            this.spinner.hide()
            this.dataForm.setValue(data.data)

        })
    }

    dataForm: FormGroupTypeSafe<Pelamar>
    form() {
        this.dataForm = this.fb.group<Pelamar>({
            id_permintaan_pegawai: new FormControl('', [Validators.required]),
            nama_belakang: new FormControl(''),
            nama_depan: new FormControl(''),
            hp: new FormControl(''),
            email: new FormControl(''),
            Spesialisasi: new FormControl(''),
            kewarganegaraan: new FormControl(''),
            id_dokumen: new FormControl(''),
            foto: new FormControl(''),
            id_user: new FormControl(''),
            id_pelamar: new FormControl(''),
            status_perkawinan: new FormControl(''),
            jenis_kelamin: new FormControl(''),
            tanggal_lahir: new FormControl(''),
            tempat_lahir: new FormControl(''),
            agama: new FormControl(''),
            phone: new FormControl(''),
            hp2: new FormControl(''),
            majors: new FormControl(''),
            id_perusahaan: new FormControl(''),
            status : new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataPelamar = this.dataForm.value
        this.direktoriPelamarService
            .putDirektoriPelamarsId({ id: this.id_pelamar, body: this.dataPelamar })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pelamar/list'])
    }
}
